// src/Navbar.js
import React from 'react';
import { Layout, Menu } from 'antd';
import { Link } from 'react-router-dom';

const { Header } = Layout;

const Navbar = () => {
  return (
    <Layout>
      <Header style={{ backgroundColor: 'black' }}>
        <Menu
          mode="horizontal"
          defaultSelectedKeys={['1']}
          style={{
            display: 'flex',
            justifyContent: 'center',
            backgroundColor: 'black',
            border: 'none',
          }}
        >
          {/* Grouped container for left-aligned nav items */}
          <div style={{ position: 'absolute', left: 0, display: 'flex' }}>
            <Menu.Item key="1" style={{ color: 'white' }}>
              <Link to="/" style={{ color: 'white', fontFamily: 'Montserrat, sans-serif' }}>
                <span style={{ fontSize: '14px', fontWeight: 'bold' }}>
                  Malkza Cave
                </span>
              </Link>
            </Menu.Item>
            <Menu.Item key="2" style={{ color: 'white' }}>
              <Link to="/parable" style={{ color: 'white', fontFamily: 'Montserrat, sans-serif' }}>
                <span style={{ fontSize: '14px', fontWeight: 'bold' }}>
                  Parable
                </span>
              </Link>
            </Menu.Item>
          </div>
        </Menu>
      </Header>
    </Layout>
  );
};

export default Navbar;