import { useState, useEffect } from 'react';

const DualCurrency = () => {

  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth <= 768);
    window.addEventListener('resize', handleResize);
    handleResize(); // Initial check
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <div>
        
        <div style={{paddingLeft: isMobile ? '14px' : '50px' }}>
            <h1 style={{fontSize: '30px', fontFamily: 'Tinos', color:'black', textAlign: isMobile ? 'left' : 'center', marginBottom: isMobile ? '14px' : '50px'}}>Why We Dont Prefer Tax to Fund Government</h1>
                <h2 style={{fontSize: '22px', fontFamily: 'Tinos', color:'black'}}>1. Impact to Other Companies and Players</h2>
                <p style={{ fontSize: '16px', lineHeight: '1.2', fontFamily: 'Roboto', color: '#5c5c5a', marginRight: isMobile ? '14px' : '50px'}}>
                Tax is one of source revenue for government, that was the only source revenue in classical age. Tax was previously used by authority or king to get income to run their regime, until last centuries many great people arise and economist says that Tax should used to fund government to take care of its people not its regime. But right now Tax is not main source of revenue for government, government could issue debt as in US it was Treasury Bond which raise fund from Global Market.
                <br></br><br></br>
                Tax was become issue in early days of last few centuries as means to distribute wealth from rich to poors. It works, poverty rate decrease as we move into more mature decades, its always improving. But too much tax will have bad impact for economy overall, for example by increasing Tax then company lose competitive advantage in research or doing another program. The core value with Tax is actually not reduce poverty rate overall but increasing whole country's standard of living, if research progress stalled then though people good in numbers inside their wallet but standard of living not increased. 
                <br></br><br></br>
                There is Utopia i believe that the core of economic policy isn't reduce poverty but increase standard of living, like people move on from deadly job into pleasant job. Its to work but your work environment is improving like there is cooling or heating system and your risk of accident in job near zero. For example, by company have budget in research, Mining Company could employ robotic mining activity controlled from miles in Command Center instead of getting hand dirty. This cannot achieved if companies dont have budget for research and development.
                <br></br><br></br>
                Tax dont have ROI while Treasury Bond have ROI or Present Value, this was good part i liked. Right now government have other source of income from issuing bond and this policy is perfectly targeting players in market, for example increase VAT by 2% will impact CPI overall but by telling companies to cooperate on funding Treasury Bond will target specific players in market without harming economy. Other thing why companies like to do this because buying Treasury Bond have ROI, though its Present Value not usually profitable but its better than lose 100% on your money in Tax.
                <br></br><br></br>
                Other issue why we prefer to fund by Special Treasury Bond is we dont wanna harm other companies. Each company is unique that there is different efficiency on developing product or how incentize they gave to employee, by try to hike tax only for targeting specific company is doing intervention in economy for other player to lose competitive advantage. There is good example in Banana Country which have ban on Liqour but actually Local Government doing intervention that there are only few 6 company that survive from this regulation while other competitor cant survive.
                </p>

                <h2 style={{fontSize: '22px', fontFamily: 'Tinos', color:'black'}}>2. Restructurize National Debt</h2>
                <p style={{ fontSize: '16px', lineHeight: '1.2', fontFamily: 'Roboto', color: '#5c5c5a', marginRight: isMobile ? '14px' : '50px'}}>
                By end of 2024, National Debt of United States is $33 Trillions. This was due to government issue more funding for their program. Turn out by raising tax alone its not enough to fund tons of program. And the magic is that if you raise most money on Global Market in your currency then your currency will be stronger and stable. The other magic is that if at Q2 Government raise $200 Billion then it attract more investor to raise bigger budget at Q3 and Q4. Though this become problem in some economic cycle which demand higher Interest Rate.
                <br></br><br></br>
                The problem within piling National Debt is Outstanding Interest Payment it need to pay next month, this will pile up to more debt if Interest Rate not improved. At end of decade, National Debt will be increasing if Outstanding Interest Payment not solved. This solution is called National Debt Restructurization.
                <br></br><br></br>
                National Debt Restructurization is done when Average Interest Rate and Face Value of Debt Raised in one Financial Year is lower than previous years, its great if difference was contract. This contrast is produced when Average Interest Rate on total debt is lower than previous year thus will make National Debt at end of decade to be lower than at start of decade.
                <br></br><br></br>
                Government will issue more debt and we cant stop them to do that, it was important to show demand of their currency. What we could help is by helping Outstanding Interest Payment to be lower next year, by buying Special Treasury Bond. This Special Treasury Bond will be lower interest rate than common Treasury Bond. We will start buying these Special Treasury Bond after we got excess of cashflow.
                <br></br><br></br>
                You cant do this with doing Tax Hike, Tax Hike alone could only lower amount of Debt government issued in Financial Year but it cant reduce Outstanding Interest Payment on National Debt. Though Tax Hike alone will not cover all Government Expense, while Special Treasury Bond will attract more funding from another investors.
                </p>

                <h2 style={{fontSize: '22px', fontFamily: 'Tinos', color:'black'}}>3. Below 0.5% Long Maturity Rate</h2>
                <p style={{ fontSize: '16px', lineHeight: '1.2', fontFamily: 'Roboto', color: '#5c5c5a', marginRight: isMobile ? '14px' : '50px'}}>
                When we start buying Special Treasury Bond, what we mean is Special Treasury Bond at Interest Rate below 0.5%. We prefer long maturity bond above 20 years to reduce our risk. We dont care whether The Fed benchmark interest rate is 5% or 12% we will keep buying this Special Treasury Bond at large face value. The point of The Fed increasing Interest Rate is to attract investors but we commit to buy Special Treasury Bond no matter its The Fed Rate, as long we have capacity to do so.
                <br></br><br></br>
                This act of buying Special Treasury Bond will have impact on Average Interest Rate National Debt issued in one financial year, the lower the rate then the lower its National Debt at end of decade. Since it have impact on Outstanding Interest Payment and it decide amount of debt government will issued to pay maturing debt. And at the end of decade, lower Average Interest Rate will produce lower National Debt.
                <br></br><br></br>
                This action also have snowballing effect, when we buy Special Treasury Bond at below 0.5% rate it have impact on Market Yield of Treasury Bond. The lower Market Yield it is from Coupon Rate, the more attractive US Debt to Global Market. When US Debt more attractive to other Currency Debt, it make US Dollar stronger and stable. Treasury Bond will have better ROI than other debt, it will be more tradeable and high demand in Global Market then US Dollars will keep as Reserve Currency.
                </p>

                <h2 style={{fontSize: '22px', fontFamily: 'Tinos', color:'black'}}>4. Increase Country Image in Global Market</h2>
                <p style={{ fontSize: '16px', lineHeight: '1.2', fontFamily: 'Roboto', color: '#5c5c5a', marginRight: isMobile ? '14px' : '50px'}}>
                Country known for their tax on companies will lose its attraction to Global Market for investor to invest and to do business, this will reduce GDP and Economic Growth overall. By contrast, countries which very good to get alternative to fund its government expense without sacrificing its global attractivity will perform the most.
                <br></br><br></br>
                We want United States of America not known as country which heavily punish company by large tax hike, instead we want it to known as country which think different and have alternative to solve its funding without sacrificing company competitiveness in their market. While we dont oppose tax, but we prefer using Special Treasury Bond to giving back to society.
                <br></br><br></br>
                The point about Tax is to distribute wealth from rich to poor, but this purpose was purely to increase Standard of Living. When a country lose its attraction to Global Companies with great product, its standard of living will be relatively lower than country which attract best company and talents. Though people have good numbers inside their wallet but they still live poorly. When you compare the richest person in Ancient Egypt with average American, Average American will live more pleasant because increase of standard of living.
                </p>
        </div>
    </div>
  );
};

export default DualCurrency;
